import React, {Suspense, Component } from 'react';
import {Route } from 'react-router-dom';
// import Dashboard from '../../pages/main-page/index';
// const Dashboard = React.lazy(()=>import('../../pages/main-page/index'))

class SubRoute extends Component {
	render() {	
		return (
			<Suspense fallback={<div>Loading...</div>}>
			  <Route path={'/'} component={ React.lazy(()=>import('../../pages/main-page/index'))} />
			</Suspense>
		);
   }
}
export default SubRoute;